export default {
	name:"ItemBuy",
	props: {
		list:null
	},
	data() {
		return {
			thisType:'USDT'
		}
	},
	methods: {
		tellMaskShow() {
			uni.$emit('ShowMask',{list:this.list})
		}
	},
	computed:{
		getType() {
			if(this.list.coinType === 0) {
				return "USDT"
			}else if(this.list.coinType === 1) {
				return "XEV"
			}
		}
	},
	watch: {
		list(val) {
			console.log(val);
			if(list.coinType === 0) {
				this.thisType = "USDT"
			}else if(list.coinType === 1) {
				this.thisType = "XEV"
			}
		}
	}
}
